@import url("https://fonts.googleapis.com/css2?family=Kalam:wght@300;400&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  overflow-y: hidden;
}

.middle {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background: #07031a;
}
.heading {
  font-size: 43px;
  color: #ecf4e0;
  font-family: "Kalam", cursive;
  letter-spacing: 2px;
}
.btn {
  margin-top: 15px;
  font-size: 20px;
  padding: 9px 37px;
  border: none;
  border-radius: 6px;
}
.samiul {
  color: #787878;
  position: absolute;
  top:45px;
  font-family: "Kalam", cursive;
}

.Sima {
  color: white;
  font-family: "Kalam", cursive;
  position: absolute;
  top: 10px;
  font-size: 30px;
}
